ol li ul,
ol li ol {
  margin: 3px 0px 0px;
}
.checkbox {
  vertical-align: middle;
  display: inline-block;
}
.checkbox input[type="checkbox"] {
  display: none;
}
.checkbox input[type="checkbox"]:checked + span {
  background: #fae103;
  border-color: #fae103;
}
.checkbox input[type="checkbox"]:checked + span i {
  color: #000;
}
.checkbox span {
  display: block;
  width: 20px;
  height: 20px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  background: #fff;
  border-radius: 3px;
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
  position: relative;
}
.checkbox span i {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  color: #fff;
  font-size: 15px;
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
}
.radio {
  vertical-align: middle;
  display: inline-block;
}
.radio input[type="radio"] {
  display: none;
}
.radio input[type="radio"]:checked + .radio-fake {
  background: #fae103;
  border-color: #fae103;
}
.radio input[type="radio"]:checked + .radio-fake i {
  color: #000;
}
.radio-fake {
  display: block;
  width: 20px;
  height: 20px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: solid 2px #a4a4a4;
  background: #fff;
  border-radius: 100%;
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
  position: relative;
}
.radio-fake i {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  color: #fff;
  font-size: 13px;
  -webkit-transition: 0.3s ease all;
  transition: 0.3s ease all;
}
.radio-text {
  display: inline-block;
  vertical-align: middle;
  font-size: 17px;
  padding-left: 8px;
}
